.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  &__body {
    max-width: 500px;
    margin: 40px auto;
  }
  &__content {
    position: relative;
    width: 100%;
    padding: 40px;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    max-height: 80vh;
    overflow-y: auto;
  }
  &__close {
    position: absolute;
    top: 8px;
    right: 14px;
    font-size: 30px;
    color: #000;
    opacity: 0.5;
    font-weight: 700;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  &__title {
    text-align: center;
    font-size: 22px;
    text-transform: uppercase;
  }
  &__input {
    display: block;
    margin: 20px auto 20px auto;
    width: 280px;
    height: 50px;
    background: #ffffff;
    box-shadow: 0px 4px 15px rgb(0 0 0 / 20%);
    border: none;
    font-size: 18px;
    text-align: center;
    padding: 0 20px;
    outline: none;
  }
  &__button {
    display: block;
    height: 50px;
    width: 280px;
    margin: 0 auto;
    background-color: #62d0df;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    box-shadow: 0px 4px 15px rgb(0 0 0 / 20%);
    cursor: pointer;
    transition: 0.3s all;
    outline: none;
  }
}

.show {
  display: block;
}

.hide {
  display: none;
}
